
.multiselect input.multiselect__input {
  display: none;
}
.multiselect.multiselect--active input.multiselect__input {
  display: block;
  width: 97% !important;
}
.with-multiselect .multiselect__input::placeholder {
  font-size: 15px;
  font-weight: normal;
  color: #DBDBDB;
  opacity: 1;
}
.with-multiselect .multiselect__input:-ms-input-placeholder {
  font-size: 15px;
  font-weight: normal;
  color: #DBDBDB;
}
.with-multiselect .multiselect__input::-ms-input-placeholder {
  font-size: 15px;
  font-weight: normal;
  color: #DBDBDB;
}
.with-multiselect .custom__tag {
  background: #f7f7f7;
  padding: 4px 8px;
  border-radius: 3px;
  display: inline-block;
  margin: 1px 5px 2px 0;
  border: 1px solid #e8eaec;
}
.with-multiselect .custom__tag span {
  color: #72777c;
}
.with-multiselect .custom__tag .custom__remove {
  color: #999;
  font-weight: bold;
}
.with-multiselect .custom__tag .custom__remove:hover {
  color: #333;
  cursor: pointer;
}
.with-multiselect .multiselect__element {
  margin: 0;
}
.with-multiselect .multiselect__element .multiselect__option {
  min-height: 32px;
  line-height: 12px;
}
.with-multiselect .multiselect__element .multiselect__option:after {
  line-height: 35px;
}
.with-multiselect .multiselect__element .multiselect__option span {
  font-size: 15px;
}
.with-multiselect .multiselect__single {
  font-size: 15px;
  color: #555;
  line-height: 2;
  margin: 0;
}
.with-multiselect .multiselect__tags {
  height: auto;
  min-height: 35px;
  border-radius: 3px;
  padding: 2px;
}
.with-multiselect .multiselect__tags .multiselect__spinner {
  height: 32px;
}
.with-multiselect .multiselect__tags .multiselect__input {
  border: 0;
  box-shadow: none;
}
.with-multiselect .multiselect__select {
  height: 33px;
}
.with-multiselect .multiselect__placeholder {
  padding-top: 0;
  margin: 6px 0 0 4px;
}
.with-multiselect .multiselect__option--selected {
  color: rgba(45, 140, 240, 0.9) !important;
  font-weight: normal;
}
.with-multiselect .multiselect__option--highlight {
  background: #f3f3f3 !important;
  color: #515a6e !important;
}
.with-multiselect .multiselect__option--highlight:after {
  content: '';
}
.with-multiselect .multiselect__option--selected.multiselect__option--highlight:after {
  background: #f3f3f3 !important;
  content: "\F158" !important;
  color: #ff6a6a !important;
  font-family: dashicons !important;
}
.with-multiselect .multiselect__option--selected:after {
  content: "\F147" !important;
  font-size: 20px !important;
  color: rgba(45, 140, 240, 0.9) !important;
  font-family: dashicons !important;
}
.with-multiselect .multiselect--disabled .multiselect__current,
.with-multiselect .multiselect--disabled .multiselect__select {
  background: none;
}

.settings-button {
  background: #1a9ed4;
  color: #fff;
  border: 0;
  border-radius: 3px;
  padding: 6px 30px;
  margin: 20px 0 15px;
  cursor: pointer;
  position: relative;
  outline: 0;
  float: left;
}
.settings-button.pull-left {
  float: left;
}
.settings-button.pull-right {
  float: right;
}
.settings-button.disabled {
  background: #B3E5FC;
  cursor: default;
}
.settings-button:not(.disabled)::before {
  content: '';
  position: absolute;
  right: 8px;
  top: 50%;
  margin-top: -11px;
  width: 20px;
  height: 20px;
  border: 4px solid #84FFFF;
  border-left-color: transparent;
  border-radius: 50%;
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  opacity: 0;
  -moz-animation-duration: 1s;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -moz-animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -moz-animation-name: rotate;
  -webkit-animation-name: rotate;
  animation-name: rotate;
  -moz-animation-timing-function: linear;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
}
.settings-button:not(.disabled):after {
  content: '';
  display: inline-block;
  height: 100%;
  width: 0px;
}
.settings-button:not(.disabled).working {
  pointer-events: none;
  cursor: not-allowed;
}
.settings-button:not(.disabled).working:not(.expand) {
  text-align: left;
}
.settings-button:not(.disabled).working:before {
  filter: progid:DXImageTransform.Microsoft.Alpha(enabled=false);
  opacity: 1;
}
.settings-button:not(.disabled).working:after {
  width: 20px;
}
@keyframes rotate {
0% {
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
}
100% {
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
}
}
